import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";

export interface ActivityRestaurantCount {
  activityId: string;
  count: number;
}

export const useActivityRestaurantCounts = () => {
  return useQuery({
    queryKey: ["activity-restaurant-counts"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("activity_restaurants")
        .select("activity_id");

      if (error) throw error;

      // Count restaurants per activity
      const counts: Record<string, number> = {};
      data.forEach((item) => {
        counts[item.activity_id] = (counts[item.activity_id] || 0) + 1;
      });

      return counts;
    },
  });
};
